@import url('https://fonts.googleapis.com/css?family=Arimo:400,400i,700&subset=hebrew');

body,
html {
  width: 100%;
  position: fixed;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Arimo', sans-serif;
  direction: rtl;
}

input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select,
select:focus,
textarea {
  font: inherit;
  border-radius: 0;
  margin: 0;
  padding: 0;
  background: none;
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  white-space: no-wrap;
  overflow: visible;
}

label {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

input[type="date"] {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
}



